
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,400i,700,700i);
@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,400i,700,700i,900,900i);
@import url(https://fonts.googleapis.com/css?family=Quicksand:400,700);
@import url(https://fonts.googleapis.com/css?family=VT323);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline
}
article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block
}
body{line-height:1;font-size:16px;-webkit-font-smoothing:antialiased;-moz-font-smoothing:antialiased;-o-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale
}
ol,ul{list-style:none;clear:both
}
blockquote,q{quotes:none
}
blockquote:before,blockquote:after,q:before,q:after{content:'';content:none
}
table{border-collapse:collapse;border-spacing:0
}
a{text-decoration:none
}
em{font-style:italic
}
strong{font-weight:bold
}
*,*:before,*:after{box-sizing:border-box
}
.nav,.pagination,.carousel,.panel-title a{cursor:pointer
}
.grey-bg{background-color:#f7f8fc
}
a{color:#b98a5b
}
.fade-enter-active,.fade-leave-active{transition:opacity .5s
}
.fade-enter,.fade-leave-active{opacity:0
}
.list-enter,.list-leave-to,.list-leave-active{opacity:0;transition:0.5s ease all
}
.list-leave-active{position:absolute
}
.fadeup-enter-active,.fadeup-leave-active{transition:all .5s ease;transform:translate3d(0,0,0);-ms-transform:translate3d(0,0,0)
}
.fadeup-enter,.fadeup-leave-active{transition:all .5s ease;transform:translate3d(0,30px,0);-ms-transform:translate3d(0,30px,0);opacity:0
}
.fadedown-enter-active,.fadedown-leave-active{transition:all .5s ease;transform:translate3d(0,0,0);-ms-transform:translate3d(0,0,0)
}
.fadedown-enter,.fadedown-leave-active{transition:all .5s ease;transform:translate3d(0,-30px,0);-ms-transform:translate3d(0,-30px,0);opacity:0
}
.slowfade-enter-active,.slowfade-leave-active{transition:all 3s linear
}
.slowfade-enter,.slowfade-leave-active{transition:all 3s linear;opacity:0
}
.fadedelay-enter-active,.fadedelay-leave-active{transition:all 2s linear;transition-delay:1.5s
}
.fadedelay-enter,.fadedelay-leave-active{transition:all 2s linear;opacity:0
}
.consoletransition-enter-active{transition:0.5s ease all;transition-delay:1s
}
.consoletransition-leave-active{transition-delay:.75s
}
.consoletransition-enter{opacity:0;transition:0.5s ease all;transition-delay:1s
}
.consoletransition-leave-active{transition:0.5s ease all;opacity:0
}
.glitch-enter-active,.glitch-leave-active{animation:glitch-on 4s linear
}
.glitch-enter,.glitch-leave-active{animation:glitch-on 4s linear
}
@keyframes sound-on{
0%{height:30%
}
20%{height:100%
}
60%{height:30%
}
80%{height:90%
}
100%{height:30%
}
}
@keyframes rotate{
from{transform:rotate(0deg)
}
to{transform:rotate(360deg)
}
}
@keyframes glitch-on{
0%{transform:scale(1, 0.8) translate3d(0, 0, 0);filter:brightness(30);opacity:1
}
3.5%{transform:scale(1, 0.8) translate3d(0, 100%, 0)
}
3.6%{transform:scale(1, 0.8) translate3d(0, -100%, 0);opacity:1
}
9.0%{transform:scale(1, 0.8) translate3d(0, 100%, 0)
}
9.1%{transform:scale(1, 0.8) translate3d(0, -100%, 0);opacity:1
}
15%{transform:scale(1, 0.8) translate3d(0, 100%, 0)
}
15.1%{transform:scale(1, 0.8) translate3d(0, -100%, 0);opacity:1
}
30%{transform:scale(1.3, 0.6) translate3d(0, 100%, 0);filter:brightness(30);opacity:0
}
31%{transform:scale(1, 1) translate3d(0, 0, 0);filter:contrast(0) brightness(0);opacity:0
}
100%{transform:scale(1, 1) translate3d(0, 0, 0);filter:contrast(1) brightness(1) saturate(1);opacity:1
}
}
#our-services .icon{width:auto
}
#our-services .icon.strategy{display:-moz-flex;display:-ms-flexbox;display:flex;-ms-flex-align:center;-moz-align-items:center;align-items:center;height:200px
}
#our-services .icon.strategy .left{height:100%;-moz-flex:1 1 70%;-ms-flex:1 1 70%;flex:1 1 70%;background-position:0% 0%;background-size:contain;background-repeat:no-repeat;background-image:url("../assets/strategy-icon.svg")
}
#our-services .icon.strategy .right{border:5px solid white;padding:5px;margin-left:-50px;height:90%;position:relative;-moz-flex:1 1 40%;-ms-flex:1 1 40%;flex:1 1 40%;background-position:50% 0%;background-size:auto 100%;background-repeat:no-repeat;background-image:url("../assets/flower-bloom.gif")
}
#our-services .icon.strategy .right::after{content:'';position:absolute;top:-5px;left:-5px;width:calc(100% + 10px);height:calc(100% + 10px);border:2px solid black
}
#our-services .icon.design{height:16vw;width:100%;border:2px solid black;padding:15px;position:relative
}
#our-services .icon.design .tooltip{position:absolute;top:-50px;left:15px;width:calc(100% - 30px);background-color:#ed6372;padding:5px;color:white;text-align:center;z-index:9;font-size:13px
}
#our-services .icon.design .tooltip::before{content:'';width:20px;height:20px;background-color:#ed6372;position:absolute;bottom:-3px;left:50%;transform:translate(-50%, 0) rotate(45deg);z-index:-1
}
#our-services .icon.design #model-window{height:100%;border:2px solid black;cursor:pointer
}
#our-services .icon.design #model-window .toolpanel{position:absolute;left:30px;top:40px;display:-moz-flex;display:-ms-flexbox;display:flex;-moz-flex-direction:row;-ms-flex-direction:row;flex-direction:row;padding:5px;border:2px solid black
}
#our-services .icon.design #model-window .toolpanel::after{content:'';position:absolute;top:-10px;left:-2px;height:10px;width:calc(100% + 4px);border:2px solid black
}
#our-services .icon.design #model-window .toolpanel::before{content:'';position:absolute;top:-6px;left:6px;height:3px;width:3px;background-color:black
}
#our-services .icon.design #model-window .toolpanel .tool{border:2px solid black;width:30px;height:30px;display:inline-block;-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer
}
#our-services .icon.design #model-window .toolpanel .tool:first-of-type{border-right:0
}
#our-services .icon.design #model-window .toolpanel .tool.brush{background-position:50% 50%;background-size:100% 100%;background-repeat:no-repeat;background-image:url("../assets/brush.svg")
}
#our-services .icon.design #model-window .toolpanel .tool.eraser{background-position:50% 50%;background-size:100% 100%;background-repeat:no-repeat;background-image:url("../assets/eraser.svg");transition:0.15s ease all
}
#our-services .icon.design #model-window .toolpanel .tool.eraser:hover{background-color:#ed6372;transition:0.15s ease all
}
#our-services .icon.design::after{position:absolute;display:block;border:2px solid black;content:'';width:100px;height:20px;bottom:-20px;left:50%;transform:translate(-50%, 0);-ms-transform:translate(-50%, 0)
}
#our-services .icon.design::before{position:absolute;display:block;border:2px solid black;content:'';width:10px;height:10px;bottom:2px;border-radius:100%;left:50%;transform:translate(-50%, 0);-ms-transform:translate(-50%, 0)
}
#our-services .icon.development{height:16vw;width:100%;border:2px solid black;position:relative
}
#our-services .icon.development #vr-window{width:100%;height:100%;position:absolute;left:0;top:0
}
#our-services .icon.development::after{position:absolute;display:block;content:'';width:100%;height:100%;background-position:50% 0%;background-size:auto 120%;background-repeat:no-repeat;background-image:url("../assets/vr-icon.svg")
}



